body {
  margin: 0;
  padding: 0;
  color: #212121;
  font-family: 'Roboto', sans-serif;
}

#topBar {
    height: 20vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#topBarHead {
    margin-left: 5%;
    font-weight: 300;
    color: #FFF;
    margin-bottom: 0.5rem;
}

#topBarQrLink {
    font-size: 0.8rem;
    margin-left: 5.5%;
    color: #F1F1F1;
    transition: color 0.2s ease;
    cursor: pointer;
}

#topBarQrLink:hover {
    color: #FFF;
}

#qrCode {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 20rem;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #FFF;
}
.fileCard {
    height: 100%;
    z-index: -10;
    display: flex;
}

.fileCard:hover {
    cursor: pointer;
}

.fileCard.noImage {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.fileCardContent{
    flex: 1 0 auto;
}

.fileCardContent.noImage {
    display: flex;
    justify-content: center;
    flex-direction: column;
    min-height: 50px;
    width: 100%;
}

.fileNameText {
    position: relative;
    z-index: 2;
}

.fileCardImage {
    width: 100%;
    height: 100%;
}

#fileListGrid {
    width: 90%;
    margin-left: 5%;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}

.fileListPaper {
    padding: 16px;
    box-sizing: border-box;
    width: 90%;
    margin-left: 5%;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}

#fileListTopBackground {
    background: #03A9F4;
    height: 3vh;
    width: 100%;
    position: absolute;
    z-index: -1;
}
